'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/lib/contexts/AuthContext';
import Navbar from '@/components/Navbar';
import { useRouter } from 'next/navigation';
import toast from 'react-hot-toast';
import Link from 'next/link';

interface CartItem {
    product: {
        _id: string;
        name: string;
        price: number;
        images: string[];
    };
    quantity: number;
    price: number;
}

export default function CheckoutPage() {
    const { user, loading: authLoading } = useAuth();
    const router = useRouter();
    const [cartItems, setCartItems] = useState<CartItem[]>([]);
    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(true);
    const [submitting, setSubmitting] = useState(false);

    const [formData, setFormData] = useState({
        shippingAddress: {
            fullName: '',
            addressLine1: '',
            addressLine2: '',
            city: '',
            state: '',
            postalCode: '',
            country: 'USA',
            phone: '',
        },
        paymentMethod: 'Credit Card',
        notes: '',
    });

    useEffect(() => {
        if (!authLoading && !user) {
            router.push('/login?redirect=/checkout');
        } else if (!authLoading && user) {
            fetchCart();
        }
    }, [user, authLoading]);

    const fetchCart = async () => {
        try {
            const res = await fetch('/api/cart');
            const data = await res.json();
            if (data.success) {
                if (data.data.items.length === 0) {
                    router.push('/cart');
                    return;
                }
                setCartItems(data.data.items);
                setTotal(data.data.total);
            }
        } catch (error) {
            toast.error('Failed to load cart');
        } finally {
            setLoading(false);
        }
    };

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        if (name.includes('.')) {
            const [parent, child] = name.split('.');
            setFormData(prev => ({
                ...prev,
                [parent]: {
                    ...(prev as any)[parent],
                    [child]: value
                }
            }));
        } else {
            setFormData(prev => ({ ...prev, [name]: value }));
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setSubmitting(true);

        try {
            const res = await fetch('/api/orders', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(formData),
            });

            const data = await res.json();

            if (data.success) {
                toast.success('Order placed successfully!');
                router.push(`/orders/${data.data._id || ''}`);
            } else {
                toast.error(data.error || 'Failed to place order');
            }
        } catch (error) {
            toast.error('Something went wrong');
        } finally {
            setSubmitting(false);
        }
    };

    if (loading || authLoading) return <div className="min-h-screen bg-gray-50"><Navbar /><div className="p-20 text-center">Preparing checkout...</div></div>;

    const tax = total * 0.1;
    const shippingCost = total > 100 ? 0 : 10;
    const grandTotal = total + tax + shippingCost;

    return (
        <div className="min-h-screen bg-gray-50">
            <Navbar />
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <h1 className="text-4xl font-black text-gray-900 mb-10">Checkout</h1>

                <form onSubmit={handleSubmit} className="grid grid-cols-1 lg:grid-cols-2 gap-12">
                    {/* Shipping Details */}
                    <div className="space-y-8">
                        <div className="bg-white rounded-3xl p-8 shadow-sm border border-gray-100">
                            <h2 className="text-2xl font-bold text-gray-900 mb-6 flex items-center gap-2">
                                <span className="w-8 h-8 bg-purple-100 text-purple-600 rounded-lg flex items-center justify-center text-base">1</span>
                                Shipping Address
                            </h2>
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                <div className="md:col-span-2">
                                    <label className="block text-sm font-bold text-gray-700 mb-2">Full Name</label>
                                    <input type="text" name="shippingAddress.fullName" value={formData.shippingAddress.fullName} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div className="md:col-span-2">
                                    <label className="block text-sm font-bold text-gray-700 mb-2">Address Line 1</label>
                                    <input type="text" name="shippingAddress.addressLine1" value={formData.shippingAddress.addressLine1} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div className="md:col-span-2">
                                    <label className="block text-sm font-bold text-gray-700 mb-2">Address Line 2 (Optional)</label>
                                    <input type="text" name="shippingAddress.addressLine2" value={formData.shippingAddress.addressLine2} onChange={handleChange} className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div>
                                    <label className="block text-sm font-bold text-gray-700 mb-2">City</label>
                                    <input type="text" name="shippingAddress.city" value={formData.shippingAddress.city} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div>
                                    <label className="block text-sm font-bold text-gray-700 mb-2">State / Province</label>
                                    <input type="text" name="shippingAddress.state" value={formData.shippingAddress.state} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div>
                                    <label className="block text-sm font-bold text-gray-700 mb-2">Postal Code</label>
                                    <input type="text" name="shippingAddress.postalCode" value={formData.shippingAddress.postalCode} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                                <div>
                                    <label className="block text-sm font-bold text-gray-700 mb-2">Phone Number</label>
                                    <input type="tel" name="shippingAddress.phone" value={formData.shippingAddress.phone} onChange={handleChange} required className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" />
                                </div>
                            </div>
                        </div>

                        <div className="bg-white rounded-3xl p-8 shadow-sm border border-gray-100">
                            <h2 className="text-2xl font-bold text-gray-900 mb-6 flex items-center gap-2">
                                <span className="w-8 h-8 bg-blue-100 text-blue-600 rounded-lg flex items-center justify-center text-base">2</span>
                                Payment Method
                            </h2>
                            <div className="space-y-4">
                                {['Credit Card', 'PayPal', 'Bank Transfer', 'Cash on Delivery'].map((method) => (
                                    <label key={method} className={`flex items-center p-4 rounded-2xl border-2 cursor-pointer transition ${formData.paymentMethod === method ? 'border-purple-600 bg-purple-50' : 'border-gray-100 hover:border-gray-200'}`}>
                                        <input type="radio" name="paymentMethod" value={method} checked={formData.paymentMethod === method} onChange={handleChange} className="w-5 h-5 text-purple-600 focus:ring-purple-500" />
                                        <span className="ml-4 font-bold text-gray-900">{method}</span>
                                    </label>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* Order Summary */}
                    <div className="lg:sticky lg:top-24 h-fit">
                        <div className="bg-white rounded-3xl p-8 shadow-xl border border-purple-50">
                            <h2 className="text-2xl font-bold text-gray-900 mb-6">Review your order</h2>
                            <div className="max-h-[300px] overflow-y-auto mb-8 pr-2 space-y-4">
                                {cartItems.map((item) => (
                                    <div key={item.product._id} className="flex justify-between items-center py-2 border-b border-gray-50 last:border-0">
                                        <div className="flex items-center gap-4">
                                            <div className="w-12 h-12 bg-gray-50 rounded-lg flex items-center justify-center p-1">
                                                <span className="text-xl">ðŸ“¦</span>
                                            </div>
                                            <div>
                                                <p className="font-bold text-gray-900 leading-tight">{item.product.name}</p>
                                                <p className="text-xs text-gray-500">Qty: {item.quantity}</p>
                                            </div>
                                        </div>
                                        <p className="font-bold text-gray-900">${(item.price * item.quantity).toFixed(2)}</p>
                                    </div>
                                ))}
                            </div>

                            <div className="space-y-4 mb-8">
                                <div className="flex justify-between text-gray-600">
                                    <span>Subtotal</span>
                                    <span className="font-bold">${total.toFixed(2)}</span>
                                </div>
                                <div className="flex justify-between text-gray-600">
                                    <span>Estimated Tax</span>
                                    <span className="font-bold">${tax.toFixed(2)}</span>
                                </div>
                                <div className="flex justify-between text-gray-600">
                                    <span>Shipping</span>
                                    <span className="font-bold text-green-600">{shippingCost === 0 ? 'FREE' : `$${shippingCost.toFixed(2)}`}</span>
                                </div>
                                <div className="border-t border-gray-100 pt-6 flex justify-between text-2xl font-black text-gray-900">
                                    <span>Total</span>
                                    <span className="text-purple-600">${grandTotal.toFixed(2)}</span>
                                </div>
                            </div>

                            <div className="mb-8">
                                <label className="block text-sm font-bold text-gray-700 mb-2">Order Notes (Optional)</label>
                                <textarea name="notes" value={formData.notes} onChange={handleChange} rows={3} className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:ring-2 focus:ring-purple-600 outline-none transition" placeholder="Add any special instructions..."></textarea>
                            </div>

                            <button type="submit" disabled={submitting} className="w-full bg-gradient-to-r from-purple-600 to-blue-600 text-white py-5 rounded-2xl font-black text-xl hover:shadow-2xl hover:scale-[1.02] transition active:scale-[0.98] disabled:opacity-50">
                                {submitting ? 'Processing Order...' : 'Place Order'}
                            </button>

                            <p className="mt-6 text-center text-xs text-gray-400">
                                By placing your order, you agree to PeakTech's Privacy Policy and Terms of Service.
                            </p>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
}
